.btn {
  background: transparent;
  max-width: 100%;
  padding: 0;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  font-style: normal;
  font-weight: bold;
  line-height: 1.35;
  letter-spacing: 0;
