  .btn--theme-access:hover:before {
    visibility: visible;
    opacity: 1;
  }
}
.btn--theme-access:before {
  background: rgba(0, 0, 0, 0.2);
  width: 100%;
  height: 100%;
  content: "";
  border-radius: inherit;
  position: absolute;
  top: 0;
  left: 0;
  transition: all 0.4s;
  visibility: hidden;
  opacity: 0;
  z-index: -1;
